"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";

const AnimeDetail = ({ params }: { params: { id: string } }) => {
  const router = useRouter();
  const { id } = params;  // Aqui estamos pegando o id diretamente da URL

  const [anime, setAnime] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  // Carregar os detalhes do anime com base no ID
  useEffect(() => {
    async function loadAnime() {
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}`);
        const json = await res.json();
        setAnime(json.data);  // Definir os dados do anime
      } catch {
        setError(true);  // Se ocorrer algum erro
      } finally {
        setLoading(false);
      }
    }
    loadAnime();
  }, [id]);  // Recarrega o anime quando o id muda

  if (loading) return <p>Carregando...</p>;
  if (error || !anime) return <p>Erro ao carregar anime.</p>;

  return (
    <div style={{ maxWidth: 900, margin: "0 auto", padding: 20 }}>
      <button
        onClick={() => router.push("/")}
        style={{
          marginBottom: 20,
          padding: "8px 12px",
          borderRadius: 6,
          cursor: "pointer",
        }}
      >
        ← Voltar
      </button>

      <div style={{ display: "flex", gap: 20, marginBottom: 20 }}>
        <img
          src={anime.images?.jpg?.image_url}
          alt={anime.title}
          width={150}
          height={200}
          style={{ objectFit: "cover", borderRadius: 6 }}
        />
        <div>
          <h1 style={{ color: "white", margin: 0 }}>{anime.title}</h1>
          <p style={{ color: "#ccc" }}>Ano: {anime.year || "Desconhecido"}</p>
          <p style={{ color: "#ccc" }}>Score: {anime.score || "N/A"}</p>
          <p style={{ color: "#ccc" }}>Status: {anime.status || "N/A"}</p>
        </div>
      </div>

      {/* Você pode adicionar outras informações aqui como descrição, episódios, etc */}
    </div>
  );
};

export default AnimeDetail;
